import React from 'react'
import { PlayCircleOutlined } from '@ant-design/icons';
import "./Film_Flip.css"
import { history } from '../../App';
export default function Film_Flip(props) {
    const { item } = props;
    return (
        <div className="flip-card mt-3">
            <div className="flip-card-inner">
                <div className="flip-card-front">
                    <img src={item.hinhAnh} alt="Avatar" style={{ width: 300, height: 300 }} />
                </div>
                <div className="flip-card-back" style={{ position: "relative", backgroundColor: "rgba(0,0,0,.9)" }} >
                    <div style={{
                        position: "absolute",
                        top: 0,
                        left: 0
                    }} >
                        <img src={item.hinhAnh} alt="Avatar" style={{ width: 300, height: 300 }} />
                    </div>
                    <div
                        className='w-full h-full'
                        style={{
                            position: "absolute",
                            backgroundColor: "rgba(0,0,0,.5)",
                            display: "flex",
                            justifyContent: "center",
                            alignItems: "center"
                        }}
                    >
                        <div>
                            <div className="rounded-full cursor-pointer">
                                <PlayCircleOutlined style={{ fontSize: "50px" }} />
                            </div>
                            <div className="text-2xl mt-2 font-bold">{item.tenPhim}</div>
                        </div>
                    </div>

                </div>
            </div>
            {/* 
            //! chỗ này dùng history hoặc thẻ NavLink để chuyển sang trang chi tiết
            */}
            <div
                onClick={() => {
                    history.push(`/detail/${item.maPhim}`)
                }}
                className="bg-orange-300 text-center cursor-pointer pb-2 bg-indigo-300 my-2 text-success-200 font-bold">ĐẶT VÉ</div>
        </div >

    )
}
